import axios from 'axios';
import { baseUrl } from './api';
/**
 * @description params
 * @params method {string} 
 * @params url {string}   /login
 * @params baseURL {string}  ******  http://xxx.com
 * @params timeout {number}   30000
 * @params params {object}  getkey
 * @params headers {string} 
 * @params withCredentials {boolean} cookies
 * @params validateStatus {func}  200 - 300
 * paramsmethod url 2params
 * @export
 * @return {Promise}
 * @class Server
 */
export default class Server {
    axios(method, url, params) {
        return new Promise((resolve, reject) => {
            if (typeof params !== 'object') params = {};
            let _option = params;
            _option = {
                method,
                url,
                timeout: 40000,
                params: null,
                data: null,
                headers: {
                    Accept: 'application/json',
                    'Content-Type': 'application/json',
                    'Access-Control-Allow-Origin': '*'
                },
                baseURL: baseUrl,
                withCredentials: false, //cookies
                validateStatus: function(status) {
                    return status >= 200 && status < 300; // default
                },
                ...params
            };
            axios.request(_option).then(
                res => {
                    resolve(typeof res.data === 'object' ? res.data : JSON.parse(res.data));
                },
                error => {
                    if (error.response) {
                        reject(error.response);
                    } else {
                        reject(error);
                    }
                }
            );
        });
    }
}



